
* {
    box-sizing: border-box;
}

.item-main-div {
    position: relative;
    width: 40%;
    height: 180px;
    /* overflow:auto; */
    /* border:2px solid greenyellow; */
    margin-bottom: 30px;
    margin-left: 7%;
    transition: transform 0.1s ease-in;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

.item-container {
    /* border:2px solid red; */
    width: 100%;
    height: 180px;
    /* display:table; */
    /* height:max-content; */
    /* height:auto; */
    position: absolute;
    /* overflow:auto; */
    margin-bottom: 10px;
    display: flex;
}

.item-main-div:hover {
    transform: scale(1.05);
}

.item-image {
    /* border:2px solid black; */
    width: 50% ;
    height: 175px;
    /* overflow:hidden; */
}

.item-description {
    position: absolute;
    text-align: justify;
    /* padding:5px; */
    /* padding-left:10px; */
    /* padding-right:10px; */
    color: blue;
    font-size: 15px;
    width: 50%;
    height: 180px;
    /* border:2px solid green; */
    right: 0;
    overflow: hidden;
    /* display:table-row; */
    padding: 10px;
}

button {
    display: inline;
    border: none;
    background: none;
    color: red;
}

.cart{
    
    /* margin-top:5%; */
    font-size:15px;
    /* border:2px solid blue; */
    /* height:25px; */
    color:white;
    background:blue;
    
}

/*---------------------Desktop view---------------------*/

@media (max-width:1400px) {
    .item-main-div,
    .item-container {
        height: 180px ;
        display: flex;
    }
    .item-image {
        margin-left: 0 ;
    }
    .item-description {
        height: 180px ;
        font-size:15px;
    }
    /*hidding the extra content*/
    #hidden-item-description{
        display:none;
    }
    
}


/* -----------------------Tablet view------------------------- */

@media (min-width:787px) and (max-width:992px) {
   
    .item-main-div,
    .item-container {
        height: 180px ;
        display: flex;
        
    }
    /*hidding the extra content*/
    #hidden-item-description{
        display:none;
    }
    .item-description{
        font-size:12px;
    }
}


/* -----------------------Mobile view------------------------- */

@media (max-width:786px) {
    .item-main-div,
    .item-container {
        height: 280px ;
        /* height:360px; */
        display: block;
    }

    /*hidding the extra content*/
    #hidden-item-description{
        display:none;
    }
    .item-main-div {
        margin-top: 20px;
    }
    .item-description {
        height: 295px ;
    }
    .item-image {
        width: 100% ;
        /* margin-left:10% ; */
    }
    .item-description {
        width: 100%;
        word-spacing: 0;
        letter-spacing: 0;
        font-size:10px;
    }
}